<template>
    <ul class="catalog__pagination pagination">
        <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === 1}" aria-label="Предыдущая страница" @click.prevent="prev()">
            &LT;
            </a>
        </li>
        <li class="pagination__item" v-for="(pageNumber, index) in pages" :key="index">
            <a class="pagination__link" :class="{'pagination__link--current': pageNumber === page}"  @click.prevent="paginate(pageNumber)">
            {{ pageNumber }}
            </a>
        </li>
        <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === pages}" href="#" aria-label="Следующая страница" @click.prevent="next()">
            &gt;
            </a>
        </li>
    </ul>
</template>

<script>
    export default {
        model: {
            prop: 'page',
            event: 'paginate'
        },
        watch: {
            count() {
                this.$emit('paginate', 1);
            }
        },
        props: ['page', 'count', 'perPage'],
        computed: {
            pages() {
                return Math.ceil(this.count / this.perPage);
            }
        },
        methods: {
            paginate(page) {
            this.$emit('paginate', page);
            },
            prev() {
            this.$emit('paginate', this.page - 1);
            },
            next() {
            this.$emit('paginate', this.page + 1);
            }
        }
    }
</script>